import { ZoneDefinition, PowerZoneModel } from '../types/zones.js';

/**
 * Calculate power zones based on FTP (Coggan 7-zone model)
 */
export function calculatePowerZones7(ftp: number): ZoneDefinition[] {
    return [
        { name: 'Active Recovery', min: 0, max: Math.round(ftp * 0.55), color: '#808080' },
        { name: 'Endurance', min: Math.round(ftp * 0.55), max: Math.round(ftp * 0.75), color: '#2196f3' },
        { name: 'Tempo', min: Math.round(ftp * 0.75), max: Math.round(ftp * 0.9), color: '#4caf50' },
        { name: 'Threshold', min: Math.round(ftp * 0.9), max: Math.round(ftp * 1.05), color: '#ffeb3b' },
        { name: 'VO2max', min: Math.round(ftp * 1.05), max: Math.round(ftp * 1.2), color: '#ff9800' },
        { name: 'Anaerobic', min: Math.round(ftp * 1.2), max: Math.round(ftp * 1.5), color: '#f44336' },
        { name: 'Neuromuscular', min: Math.round(ftp * 1.5), max: 9999, color: '#9c27b0' },
    ];
}

/**
 * Calculate power zones based on FTP (5-zone model)
 * Typical Z1-Z5 model
 */
export function calculatePowerZones5(ftp: number): ZoneDefinition[] {
    return [
        { name: 'Recovery', min: 0, max: Math.round(ftp * 0.6), color: '#808080' },
        { name: 'Endurance', min: Math.round(ftp * 0.6), max: Math.round(ftp * 0.8), color: '#2196f3' },
        { name: 'Tempo', min: Math.round(ftp * 0.8), max: Math.round(ftp * 0.95), color: '#4caf50' },
        { name: 'Threshold', min: Math.round(ftp * 0.95), max: Math.round(ftp * 1.05), color: '#ff9800' },
        { name: 'VO2max', min: Math.round(ftp * 1.05), max: 9999, color: '#f44336' },
    ];
}

/**
 * Calculate power zones based on FTP (3-zone model)
 * Polarized (Zone 1, 2, 3)
 */
export function calculatePowerZones3(ftp: number): ZoneDefinition[] {
    return [
        { name: 'Endurance', min: 0, max: Math.round(ftp * 0.75), color: '#2196f3' }, // < AeT approx
        { name: 'Threshold', min: Math.round(ftp * 0.75), max: Math.round(ftp * 1.05), color: '#ff9800' }, // Sweet spot / Threshold
        { name: 'High Intensity', min: Math.round(ftp * 1.05), max: 9999, color: '#f44336' }, // > FTP
    ];
}

/**
 * Calculate heart rate zones based on max HR (5-zone model)
 */
export function calculateHrZones(maxHr: number): ZoneDefinition[] {
    return [
        { name: 'Recovery', min: Math.round(maxHr * 0.5), max: Math.round(maxHr * 0.6), color: '#808080' },
        { name: 'Aerobic', min: Math.round(maxHr * 0.6), max: Math.round(maxHr * 0.7), color: '#2196f3' },
        { name: 'Tempo', min: Math.round(maxHr * 0.7), max: Math.round(maxHr * 0.8), color: '#4caf50' },
        { name: 'Threshold', min: Math.round(maxHr * 0.8), max: Math.round(maxHr * 0.9), color: '#ff9800' },
        { name: 'Anaerobic', min: Math.round(maxHr * 0.9), max: maxHr, color: '#f44336' },
    ];
}

export function generatePowerZones(ftp: number, model: PowerZoneModel): ZoneDefinition[] {
    switch (model) {
        case '3':
            return calculatePowerZones3(ftp);
        case '5':
            return calculatePowerZones5(ftp);
        case '7':
            return calculatePowerZones7(ftp);
        default:
            return calculatePowerZones7(ftp);
    }
}
